class ProductList extends Component {
  render() {
    const { products } = this.props;
    // console.log(products);

    return (
      <div>
        <section className="ratio_asos absolute-product section-b-space">
          <div className="title4">
            <h2 className="title-inner4">Products</h2>
            <div className="line">
              <span></span>
            </div>
          </div>
          <div className="container">
            <div className="row">
              <div className="col">
                <div className="no-slider row">
                  {products.map((product, index) => (
                    <ProductStyleNine product={product} key={index} />
                  ))}
                </div>
                {this.props.hasmore ? (
                  <p
                    onClick={this.props.fetchProducts}
                    className="seen-cls seen-it-cls"
                  >
                    <b>Load More</b>
                  </p>
                ) : (
                  <p className="seen-cls seen-it-cls">
                    <b>No more products</b>
                  </p>
                )}
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    cartItems: state.cart.shoppingCart
  };
};

const mapDispatchToProps = dispatch => {
  return {
    refreshCart: () => dispatch(fetchCart())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ProductList);
